.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}

.product_rating {
    display: flex;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background: #fffb00;
    border: 1px solid #bdb10b;
    padding: 3px 8px;
    margin-top: 10px;
    color: black;
}

.product_price {
    margin-top: 5px;
}

.product_info {
height: 100px;
margin-bottom: 15px;
}