.payment {
    background-color: white;
}

.payment_container > h1{
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234, 237, 237);
    border-bottom: 1px solid lightgray;
}

.payment_container > h1  a {
    text-decoration: none;
}

.payment_section {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}

.payment_title {
    flex: 0.2;
}

.payment_address, .payment_items, .payment_details {
    flex: 0.8;
}

.payment_details {
    max-width: 400px;
}

.payment_details > h3 {
    padding: 20px o;
}

.payment_details > form > div > button {
    background-color: #fffb00;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: #bdb10b;
    font-weight: bolder;
    margin-top: 10px;
    color: black;
}